import { Injectable } from '@angular/core';
import { FetchClient, IFetchOptions } from '@c8y/client';
import { Subject, Observable } from 'rxjs';
import { ApiCall, ApiCallOptions } from './api.model';
import { filter } from 'rxjs/operators';

@Injectable()
export class ApiService {
  calls: Observable<ApiCall>;
  private callsSubject = new Subject<ApiCall>();

  constructor(private client: FetchClient) {
    this.calls = this.callsSubject.asObservable();
    this.hookIntoClientFetch();
  }

  hookResponse(hookFilter: (call: ApiCall) => boolean) {
    return this.callsSubject.pipe(
      filter(({ phase }) => phase === 'finish'),
      filter(hookFilter)
    );
  }

  hookRequest(hookFilter: (call: ApiCall) => boolean) {
    return this.callsSubject.pipe(
      filter(({ phase }) => phase === 'start'),
      filter(hookFilter)
    );
  }

  async onFinish(call: ApiCall) {
    this.callsSubject.next({ phase: 'finish', ...call });
  }

  onStart(call: ApiCall) {
    this.callsSubject.next({ phase: 'start', ...call });
  }

  async resolveData(call: ApiCall): Promise<any> {
    const { response, method } = call;
    if ('data' in response) {
      return Promise.resolve({ data: response.data, method });
    } else {
      const cb = (data) => ({ data, method});
      return (response as Response).clone().json().then(cb, cb);
    }
  }

  private hookIntoClientFetch() {
    const fetch = this.client.fetch.bind(this.client);
    this.client.fetch = async (url, options: ApiCallOptions & IFetchOptions = { method: 'GET'}) => {
      const { method } = options;
      this.onStart({ options, method, url });
      let fetchPromise = fetch(url, options);
      if (typeof options.responseInterceptor === 'function') {
        fetchPromise = fetchPromise.then(options.responseInterceptor);
      }
      fetchPromise.then(
        (response: Response) => this.onFinish({ response, url, options, method }),
        (response: Response) => this.onFinish({ response, url, options, method })
      );
      return fetchPromise;
    };
  }
}
