import { NgModule } from '@angular/core';
import { BasicAuth, FetchClient, Realtime, CookieAuth } from '@c8y/client';
import { ApiService } from './api.service';
import * as services from './services';

function toProvider(provide) {
  let deps: any[] = [FetchClient, Realtime];
  if (provide === FetchClient)  {
    deps = [CookieAuth];
  }
  if (provide === BasicAuth || provide === CookieAuth)  {
    deps = [];
  }
  if (provide === Realtime) {
    deps = [FetchClient];
  }
  return {provide, useClass: provide, deps};
}

const providers: any[] = (Object.keys(services).map((k) => toProvider(services[k])) as any[])
  .concat([
    { provide: ApiService, useClass: ApiService, deps: [FetchClient] }
  ]);
// @dynamic
@NgModule()
export class DataModule {
  static providers() {
    return providers;
  }
  static forRoot() {
    return {
      ngModule: DataModule,
      providers
    };
  }
}
